.nav-tabs .nav-link.active {
  color: #c9e3fd;
  background-color: rgba(red, green, blue, .0);
  border-color: #dee2e6 #dee2e6 #fff;
}

.MuiTab-selected-52 {
  color: white !important;
}

.MuiTab-fullWidth-54 {
  flex-grow: 1 !important;
  max-width: none !important;
  flex-shrink: 0 !important;
}

.Tab[aria-selected="true"]{
  color: yellow;
  /* flex-grow: 2; */
}

#tabsRow{
  background-color: #104e7d !important;
}

#tabContentRow{
  background-image: url("http://barbeelawfirm.com/wp-content/uploads/2014/12/books.jpg");
  /* -webkit-filter: grayscale(75%); */
  /* filter: grayscale(75%); */
  width: 100%;
  height: auto;
  background-repeat: no-repeat;
  background-size: cover;
}

#typography{
    padding: 24px;
    height: 25rem;
    background-color: rgba(0, 0, 0, 0.6);
}
/* "http://www.duncanville.com/wp-content/uploads/2016/03/law-books.jpg" */

span{
  padding-top: .5rem;
  padding-bottom: .5rem;
  font-size: .85rem;
  text-align: center;
  color: #dfecfa;
  font-family: "Playfair Display", serif;
}

.MuiPrivateTabIndicator-root-62 {
  width: 100%;
  height: 0px;
  bottom: -12px !important;
  color: white;
  position: absolute;
  transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

.MuiTabs-fixed-42 {
  width: 100%;
  overflow-x: inherit !important; 
}

@media screen and (max-width: 600px) {
  #typography{
    padding: 24px;
